<template>
    	<div class="hero bannerimg">
				<div class="container">
					<form action="#" class="find-location">
						<input type="text" placeholder="Find your location..." :value="areaname">						
						<input type="submit" value="Find">					
					</form>              
				</div>
			</div>
</template>
<script>
import dropdown from 'vue-dropdowns';
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
export default {
        data() {
          return {
            arrayOfObjects: [],
            object: {
              name: 'Object Name',
            }
          }
        },

        components: {
            'dropdown': dropdown,
        },

        methods: {
          methodToRunOnSelect(payload) {
            this.object = payload;
          },
        },
        computed: {
           ...mapGetters({           
           areaname:'getareaname',
         }), 
        },
      }
</script>


