<template>
        	<div class="forecast-table">				
				<div class="container">
					<div class="forecast-container">
                             
						<div class="today forecast">
							<div class="forecast-header">
								<div class="day">{{timearrayFirst.startTime|convertDayOfWeek}}</div>
								<div class="date">{{timearrayFirst.startTime|ConvertDate}}</div>
							</div> <!-- .forecast-header -->
							<div class="forecast-content">
								<div class="location">{{areaname}}</div>
								<div class="degree">
									<div class="num">{{timearrayFirst.elementValue|GetWeatherTempture}}<sup>o</sup>C</div>
									<div class="forecast-icon">
										<img :src="timearrayFirst.elementValue|GetWeatherStatusPhoto" alt="" width=90>
									</div>	
								</div>
								<span><img src="@/assets/weather/images/icon-umberella.png" alt="">{{timearrayFirst.elementValue|GetRainPercent}}</span>
								<span><img src="@/assets/weather/images/icon-wind.png" alt="">{{timearrayFirst.elementValue|GetWeatherHumidity}}</span>
								<span><img src="@/assets/weather/images/icon-compass.png" alt="">{{timearrayFirst.elementValue|GetWeatherStatus}}</span>
							</div>
						</div>

						<div class="forecast" v-for="(item,key) in timearrayRest" :key="key">
							<div class="forecast-header">
								<div class="day">{{item[1].startTime|convertDayOfWeek}}</div>
							</div> <!-- .forecast-header -->
							<div class="forecast-content">
								<div class="forecast-icon">
									<a href="#" @click.prevent="targetDayDataFilter(item[1])">
									<img :src="item[1].elementValue|GetWeatherStatusPhoto" alt="" width=48>
									</a>
								</div>
								<div class="degree">{{item[1].elementValue|GetWeatherTempture}}<sup>o</sup>C</div>
								<small>{{item[1].elementValue|GetWeatherLowTempture}}<sup>o</sup></small>
								<div></div>
								<input  type="button" value="當日細節"  @click="targetDayDataFilter(item[1])">
							</div>
						</div>	


					</div>
				</div>
			</div>
</template>

<script>
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
import { mapState } from 'vuex';
export default {
  name: "app",
  props:['weathdata'],
  data() {
    return {
	   //areadata.locationName
	   //weekName:'',
    };
  },
  methods: {
	 ...mapActions({
		 targetDayDataFilter:'targetDayDataFilter',		 
	 }),
			
	 },	

  mounted(){
     
  },
  computed: {
	  ...mapGetters({		
		  //areaname:'getareaname',
		  areadata:'getareadata', //{{areadata.locationName}}
		  weatherElement:'getareaweatherElement',
		  timearray:'gettimearray',	
		  timearrayFirst:'gettimearrayFirst',	 
		  timearrayRest:'gettimearrayRest',
		  areaname:'getareaname',
	  }),	  
  },
};
</script>


