<template>    
	<div class="site-content">

      <!-- <test></test> -->
      <headerDetail :weathdata="arrayData"></headerDetail>

	    <banner></banner>

	    <oneweek :weathdata="arrayData"></oneweek>

	    <today></today>

	    <footerDetail></footerDetail>	  
			
    </div>    
</template>

<script>
import headerDetail from '@/components/weather/detail/headerDetail';
import banner from '@/components/weather/detail/banner';
import oneweek from '@/components/weather/detail/oneweek';
import today from '@/components/weather/detail/today';
import footerDetail from '@/components/weather/detail/footerDetail';
import test from '@/components/weather/detail/test';

import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
export default {
  name: "app",
  data() {
    return {
      api_key: "CWB-DF9EFEB7-9654-4094-84BC-FAFE2534E2BA",
      url_base: "https://opendata.cwb.gov.tw/api/",
      query: "",
      weather: "",
      parameter: "",
      mint: "",
      maxt: "",
	    pop: "",
      arrayData:[],      
    };
  },
  methods: {
	    ...mapActions({           
           getInformation:'getInformation',
         }),  
    fetchWeather() {    
		let api =`${this.url_base}v1/rest/datastore/F-D0047-063?Authorization=${this.api_key}&format=JSON&elementName=WeatherDescription&sort=time`;
		console.log(api);
        fetch(		 
		     api
        )
          .then((res) => {			 
            return res.json();
          })
          .then(this.setResults)
          .catch(() => {			 
            alert("可能輸入錯誤囉! ex:臺北市");
          });
    },
    setResults(results) {
       var vm=this;
		   console.log('a1111');
       console.log(results.records.locations[0])      
       var jsonObj=results.records.locations[0].location
       var obj = Object.keys(jsonObj).map(function(_) { return jsonObj[_]; });
       if(obj.length>0)
       {
         vm.arrayData=obj;
       }
       console.log(vm.arrayData);
       console.log('b222'); 

    },
  },

  mounted(){	 
   var vm=this;
   this.fetchWeather();      
  },
  computed:{
...mapGetters({           
           areaname:'getareaname',
         }), 
    getareacode(){
      var vm=this;
      // vm.areanametitle=areaname;
    },
  },
   components:{
	   headerDetail,
	   banner,
	   oneweek,
	   today,
	   footerDetail,
	   test,
	},	
};
</script>


<style lang="scss" scoped>

</style>


