<template>
    <div>
        	

			<main class="main-content" v-if="targetShow">
				<div class="fullwidth-block">
					<div class="container">					
					</div>
				</div>
				<div class="forecast-table">
					<div class="container">
						<div class="forecast-container">
							<div class="today forecast">
								<div class="forecast-header">
									<div class="day">{{targetDayData.startTime|convertDayOfWeek}}</div>
									<div class="date">{{targetDayData.startTime|ConvertDate}}</div>
								</div> <!-- .forecast-header -->
								<div class="forecast-content">
									<div class="location">{{areaname}}</div>
									<div class="degree">
										<div class="num">{{targetDayData.elementValue|GetWeatherTempture}}<sup>o</sup>C</div>
										<div class="forecast-icon">
											<img :src="targetDayData.elementValue|GetWeatherStatusPhoto" alt="" width=90>
										</div>	
									</div>
									<span><img src="@/assets/weather/images/icon-umberella.png" alt="">{{targetDayData.elementValue|GetRainPercent}}</span>
									<span><img src="@/assets/weather/images/icon-wind.png" alt="">{{targetDayData.elementValue|GetWeatherHumidity}}</span>
									<span><img src="@/assets/weather/images/icon-compass.png" alt="">{{targetDayData.elementValue|GetWeatherStatus}}</span>
								</div>
							</div>
						
						</div>
					</div>
				</div>
			</main> <!-- .main-content -->
    </div>
</template>

<script>
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex';
import { mapState } from 'vuex';
export default {
  name: "app",
  props:['weathdata'],
  data() {
    return {
	   //areadata.locationName
	   //weekName:'',
    };
  },
  methods: {
	 ...mapActions({
		 targetDayDataFilter:'targetDayDataFilter',		 
	 }),
			
	 },	
  mounted(){
     
  },
  computed: {
	  ...mapGetters({		
		  //areaname:'getareaname',
		  areadata:'getareadata', //{{areadata.locationName}}
		  weatherElement:'getareaweatherElement',
		  timearray:'gettimearray',	
		  timearrayFirst:'gettimearrayFirst',	 
		  timearrayRest:'gettimearrayRest',
		  areaname:'getareaname',
		  
		  targetDayData:'gettargetDayData',
		  targetShow:'getTargetShow',
	  }),	  
  },
};
</script>


