
export default {
    filters: {
        convertDayOfWeek: (value) => {
            //const value = datevalue;
            var subDataTimeValue=value.substring(0, 10);		 
            var weekvalue=new Date(subDataTimeValue);
            var weekNum=weekvalue.getDay();
            var weekName='';		 
            switch(weekNum)
            {
                case 0:
                     weekName = "Sunday";
                     break;
                case 1:
                     weekName = "Monday";
                     break;
                case 2:
                     weekName = "Tuesday";
                     break;
                case 3:
                     weekName = "Wednesday";
                     break;
                case 4:
                     weekName = "Thursday";
                     break;
                case 5:
                     weekName = "Friday";
                     break;
                case 6:
                     weekName = "Saturday";
                     break;
                default:
                     weekName = ""; // we don't have a valid day
            }
            return weekName;
        },
        ConvertDate:(value)=>{
            var subDataTimeValue=value.substring(0, 10);
            var month=subDataTimeValue.substr(5, 2);
            var day=subDataTimeValue.substr(8, 2);
            var fullName='';
            //console.log(month);
            //console.log(day);
            switch(month)
            {
              case '01':
                   fullName = "Jan";
                   break;
              case '02':
                   fullName = "Feb";
                   break;
              case '03':
                   fullName = "Mar";
                   break;
              case '04':
                   fullName = "Apr";
                   break;
              case '05':
                   fullName = "May";
                   break;
              case '06':
                   fullName = "Jun";
                   break;
              case '07':
                   fullName = "Jul";
                   break;
              case '08':
                   fullName = "Aug";
                   break;
              case '09':
                   fullName = "Sep";
                   break;
              case '10':
                   fullName = "Oct";
                   break;
              case '11':
                   fullName = "Nov";
                   break;
              case '12':
                   fullName = "Dec";
                   break;	  	  	  
            }
 
            return fullName+' '+day;
        },
        GetWeatherStatus:(value)=>{
            return value[0].value.split('。')[0];
        },
        SplitString:(value)=>{
            var test= value[0].value.split('。');				 	
            return value;
        },
        GetWeatherStatusPhoto:(value)=>{
            var weatherStatus=value[0].value.split('。')[0];
            var weatherPhoto='';
            switch(weatherStatus.trim())
            {
                //case '多雲':
                //weatherPhoto==require('@/assets/weather/images/icons/icon-5.svg');
                //break;
                case '陰天':
                weatherPhoto=require('@/assets/weather/images/icons/icon-6.svg');
                break;
                case '多雲時陰':
                weatherPhoto=require('@/assets/weather/images/icons/icon-7.svg');
                break;				
                //case '多雲短暫陣雨':
                //weatherPhoto=require('@/assets/weather/images/icons/icon-9.svg');
                //break;
                case '多雲短暫雨':			
                case '多雲短暫陣雨':
                case '多雲時陰短暫雨':
                weatherPhoto=require('@/assets/weather/images/icons/icon-9.svg');
                break;
                //case '多雲時陰短暫雨':
                //weatherPhoto==require('@/assets/weather/images/icons/icon-9.svg');
                //break; 
                case '陰短暫雨':
                weatherPhoto=require('@/assets/weather/images/icons/icon-11.svg');
                break;
                default:
                weatherPhoto=require('@/assets/weather/images/icons/icon-5.svg');
                break;
            }
            return weatherPhoto;
        },
        GetRainPercent:(value)=>{
            var test= value[0].value.split('。');		
            return test[1].split(' ')[1];
        },
        GetWeatherTempture:(value)=>{
            var test=value[0].value.split('。');
			var counter = -1;
            for (var i = 0; i < test.length; i++) {
			  var result=test[i].indexOf('溫度攝氏');
			  if(result !=-1)
			  {
				  counter=i;
				  break;
			  }
            }			

			if(counter !=-1)
			{
                return test[counter].split('至')[1].substring(0,2);
			}
			else
			{
				return '';
			}			
        },
        GetWeatherLowTempture:(value)=>{
            var test=value[0].value.split('。');
			var counter = -1;
            for (var i = 0; i < test.length; i++) {
			  var result=test[i].indexOf('溫度攝氏');
			  if(result !=-1)
			  {
				  counter=i;
				  break;
			  }
            }			

			if(counter !=-1)
			{
                return test[counter].split('溫度攝氏')[1].substring(0,2);
			}
			else
			{
				return '';
			}			
        },
        GetWeatherHumidity:(value)=>{
            var test=value[0].value.split('。');
			//相對濕度
            var counter = -1;
            for (var i = 0; i < test.length; i++) {
			  var result=test[i].indexOf('相對濕度');
			  if(result !=-1)
			  {
				  counter=i;
				  break;
			  }
			}
			if(counter !=-1)
			{
                return test[counter];
			}
			else
			{
				return '';
			}	
        },
        
      }//End filters
}




