<template>
      	<div class="site-header">
				<div class="container">
					<a href="index.html" class="branding">
						<img src="@/assets/weather/images/logo.png" alt="" class="logo">
						<div class="logo-type">
							<h1 class="site-title">Company name</h1>
							<small class="site-description">tagline goes here</small>
						</div>
					</a>

					<!-- Default snippet for navigation -->
					<div class="main-navigation">
						<button type="button" class="menu-toggle"><i class="fa fa-bars"></i></button>
						<ul class="menu">
							<li class="menu-item" v-for="(item,key) in weathdata" :key="key">
                                 <a href="index.html" @click.prevent="signDataFilter({data:weathdata,location:item.locationName})">{{item.locationName}}</a>
							</li>
								 <!-- <dropdown :options="arrayOfObjects" :selected="object" v-on:updateOption="methodToRunOnSelect"></dropdown> -->
						</ul> <!-- .menu -->
					</div> <!-- .main-navigation -->

					<div class="mobile-navigation"></div>

				</div>
			</div> <!-- .site-header -->
</template>
<script>
import { mapActions } from 'vuex';
import { mapGetters } from 'vuex'; 
export default {
  name: "headerDetail",
  props:['weathdata'],
  data() {
    return {
     areanametitle:'',
    };
  },
  methods: {
    ...mapActions({           
           signDataFilter:'signDataFilter',
		 }), 
	//    areaclick(objects){
	// 	   this.signDataFilter(objects);
	//    },	 
  },

  mounted(){
    
  },
  computed:{
	  ...mapGetters({           
           areaname:'getareaname',
         }), 
  },
};
</script>


