

export default{
    state:{          
         areaname:'',
         areadata:{},
         areaweatherElement:{},
         timearray:[],         
         timeArrayFirst:[],
         timeArrayRest:[],
         timeArrayRestFilter:[],
         targetDayData:[],
         targetShow:false,
    },
    getters:{     
       getareaname(state){
        return state.areaname;
       },
       getareadata(state){
        return state.areadata;
       },
       getareaweatherElement(state){
        return state.areaweatherElement;
       },
       gettimearray(state){
        return state.timearray;
       },  
       gettimearrayFirst(state){
        return state.timeArrayFirst;
       }, 
       gettimearrayRest(state){
        return state.timeArrayRest;
       },   
       gettimearraryRestFilter(state){
         return state.timeArrayRestFilter;
       },
       gettargetDayData(state){
         return state.targetDayData;
       },
       getTargetShow(state){
          return state.targetShow;
       },       
    },
    actions:{  
        signDataFilter(context,{data,location}){              
          context.commit('areachange',location);
          context.commit('areafilter',data);   
          context.commit('targetShowChange','false');       
        },
        targetDayDataFilter(context,payload){
          //console.log('ak47');
          context.commit('targetDayDataChange',payload);
          context.commit('targetShowChange','true');
        },
        //targetShowAction(context,payload){
        //  context.commit('targetShowChange',payload);
        //},        
    },
    mutations:{
      areachange(context,payload){
        context.areaname=payload;
        context.targetDayData=[];
      },
      areafilter(context,payload){
        console.log('aaaa');
        console.log('context.areaname:'+context.areaname)
        console.log(payload)
        if(context.areaname!='')
        {
            console.log('bbbb')
            var singareadata=payload.find(function(element){
               if(element.locationName==context.areaname)
               {     
                context.areadata=element;  
                context.areaweatherElement=element.weatherElement; 
                var timeAllArray=element.weatherElement[0].time.sort(function(a,b){return a.startTime-b.startTime}); 
                
                          
                var restall=timeAllArray.map(function(item,index){
                   if(index==0)
                   {                                    
                      return [{isshow:true,isFirst:true},item];
                   }
                   else
                   {
                    if(item.startTime.substring(0, 10)==item.endTime.substring(0, 10))
                    {
                      return [{isshow:true,isFirst:false},item];
                    }
                    else
                    {                     
                      return [{isshow:false,isFirst:false},item];
                    }
                   } 
                }).filter(restall=>restall[0].isshow==true && restall[0].isFirst==false);

                context.timeArrayFirst=timeAllArray[0];   
                context.timeArrayRest=restall;
                context.timearray=timeAllArray;                
                console.log(restall); 
                console.log('this is a test');           
               }                 
            }); 
        }
        else
        {
          context.areadata=[];
        }        
      },
      targetDayDataChange(context,payload){
        context.targetDayData=payload;
      },
      targetShowChange(context,payload){
        if(payload=='false')
        {
          context.targetShow=false;
        }
        else
        {
          context.targetShow=true;
        }        
      },
    }
}


